import { clearLevelUi, showMessageUi } from "./ui";
import levels from "./levels";

export function loadLevel(levelNumber) {
  return JSON.parse(JSON.stringify(levels[levelNumber]));
}

export function clearLevel() {
  clearLevelUi();
  return {};
}

export function showLevelMessage(levelMessages, messageIndex) {
  if (levelMessages && levelMessages.length) {
    if (messageIndex < levelMessages.length) {
      showMessageUi(levelMessages[messageIndex]);
    }
  }
}
