import { getNextBoardPosition, getIsPieceRaised } from "./board";
import { getOppositeDirection } from "./piece";
import { getPieceCssClassName, movePiece, updatePieceDirectionUi } from "./ui";

export function moveAi({
  aiPieces,
  boardPositionsRaised,
  boardPositionsUnavailable,
  boardSizeX,
  boardSizeY,
}) {
  return aiPieces.map((piece) => {
    const isPieceRaised = getIsPieceRaised({ piece, boardPositionsRaised });

    if (!isPieceRaised) {
      // only move if piece is not on a raised position
      const moveToSquare = getNextBoardPosition({
        boardPositionsUnavailable,
        boardSizeX,
        boardSizeY,
        currentBoardPosition: piece.boardPosition,
        direction: piece.direction,
      });

      if (moveToSquare !== -1) {
        piece.boardPosition = moveToSquare;
        const pieceDomClass = getPieceCssClassName(piece.playerId);
        movePiece(pieceDomClass, moveToSquare, boardSizeX, boardSizeY);
      }
    }

    return piece;
  });
}

// TODO there are instances when an ai will clash with another
// this would be a good one to add a test for and then refactor!
export function getAiWithDirectionsForNextMove({
  aiPieces,
  boardPositionsRaised,
  boardPositionsUnavailable,
  boardSizeX,
  boardSizeY,
}) {
  // store the next moves so you don't clash with a piece that's already moved
  let nextMoves = [];
  return aiPieces.map((piece) => {
    const isPieceRaised = getIsPieceRaised({ piece, boardPositionsRaised });

    if (!isPieceRaised) {
      // only for if a piece isn't on a raised position
      const nextMove = getNextBoardPosition({
        boardPositionsUnavailable,
        boardSizeX,
        boardSizeY,
        currentBoardPosition: piece.boardPosition,
        direction: piece.direction,
      });

      // if next move is invalid or already taken, check opposite
      if (nextMove === -1 || nextMoves.includes(nextMove)) {
        piece.direction = getOppositeDirection(piece.direction);
        updatePieceDirectionUi(piece);

        // can it move in the other direction
        const nextMoveOpposite = getNextBoardPosition({
          boardPositionsUnavailable,
          boardSizeX,
          boardSizeY,
          currentBoardPosition: piece.boardPosition,
          direction: piece.direction,
        });

        if (nextMoveOpposite === -1 || nextMoves.includes(nextMoveOpposite)) {
          console.log("it's stuck!");
        } else {
          nextMoves.push(nextMoveOpposite);
        }
      } else {
        nextMoves.push(nextMove);
      }
    }

    return piece;
  });
}
