import { ACTION_EXIT } from "./actions";

export const BOARD_CSS_CLASS_NAME = "js-castle";
export const BOARD_WIDTH_CSS_CLASS_NAME = "cm-board-width-";
export const PIECE_CSS_CLASS_FOR_JS_BASE = "js-piece";
export const PIECE_CSS_CLASS_FOR_JS = "js-piece--";
export const PIECE_MOVE_ANIMATION_TIME_MS = 300;
export const BOARD_SQUARE_CSS_CLASS_NAME = "js-cm-board__square--";
const CSS_CLASS_NAME_FOR_ACTION = "js-action";
const CSS_CLASS_NAME_MESSAGE = "js-message";
const CSS_CLASS_NAME_DIRECTION = "cm-piece-direction--";
const CSS_CLASS_NAME_BOARD_INNER = "js-cm-board__inner";

export function addActionToUi(
  action,
  boardSquareCssClassName = BOARD_SQUARE_CSS_CLASS_NAME
) {
  let { boardPosition, type } = action;
  if (action.iconSpecific) {
    type = action.iconSpecific;
  }
  document
    .querySelector(`.${boardSquareCssClassName}${boardPosition}`)
    .classList.add(`cm-action`, `js-action`, `cm-action--${type}`);
}

export function addPieceToBoardUi(
  piece,
  boardSizeX,
  boardSizeY,
  boardCssClassName = BOARD_CSS_CLASS_NAME
) {
  const pieceDOMElement = document.createElement(`div`);

  pieceDOMElement.className = generatePieceClassName(piece, boardCssClassName);
  document.querySelector(`.${boardCssClassName}`).append(pieceDOMElement);
  movePiece(
    `${boardCssClassName} .js-piece--${piece.playerId}`,
    piece.boardPosition,
    boardSizeX,
    boardSizeY
  );
}

export function clearActionUi(actionClassName, boardPosition) {
  document
    .querySelector(`.${BOARD_SQUARE_CSS_CLASS_NAME}${boardPosition}`)
    .classList.remove(
      `cm-action`,
      `js-action`,
      `cm-action--${actionClassName}`
    );
}

export function clearLevelUi() {
  removePiecesFromBoard();
  removeActionsFromBoard();
}

export function clearMessageUi() {
  document.querySelector(`.${CSS_CLASS_NAME_MESSAGE}`).innerHTML = "";
}

export function clearTargetBoardPositionsUi() {
  document.querySelectorAll(".is-target").forEach((node) => {
    node.classList.remove("is-target");
  });
}

function setBoardWidthCssClassName(boardContainerEl, boardSizeX) {
  const classNameToAdd = `${BOARD_WIDTH_CSS_CLASS_NAME}${boardSizeX}`;
  const classNameToRemove = `${BOARD_WIDTH_CSS_CLASS_NAME}`;
  // filter out the class name to be removed
  const newClassListArray = [...boardContainerEl.classList].filter(
    (className) => {
      return !className.includes(classNameToRemove);
    }
  );
  newClassListArray.push(classNameToAdd);
  boardContainerEl.classList = newClassListArray.join(" ");
}

export function createBoardUi({
  boardSizeX,
  boardSizeY,
  boardContainerClassName = BOARD_CSS_CLASS_NAME,
}) {
  const boardContainerEl = document.querySelector(
    `.${boardContainerClassName}`
  );
  const boardContainerInnerEl = document.querySelector(
    `.${boardContainerClassName} .${CSS_CLASS_NAME_BOARD_INNER}`
  );
  if (boardContainerEl && boardContainerInnerEl) {
    setBoardWidthCssClassName(boardContainerEl, boardSizeX);
    boardContainerInnerEl.innerHTML = "";

    let square = 1;
    for (let row = 1; row <= boardSizeY; row++) {
      // create rows
      const boardRow = document.createElement(`div`);
      boardRow.className = `cm-board__row`;
      for (let column = 1; column <= boardSizeX; column++) {
        // create squares
        const boardSquare = document.createElement(`div`);
        boardSquare.className = `cm-board__square js-cm-board__square--${square}`;
        square++;
        boardRow.append(boardSquare);
      }
      boardContainerInnerEl.append(boardRow);
    }
  }
}

export function generatePieceClassName(
  piece,
  boardCssClassName = BOARD_CSS_CLASS_NAME
) {
  const el = document.querySelector(
    `.${boardCssClassName} .${PIECE_CSS_CLASS_FOR_JS}${piece.playerId}`
  );

  if (el) {
    let elClassList = [...el.classList];
    piece.type.directions.forEach((direction) => {
      elClassList = elClassList.filter((value) => {
        // remove prev direction
        return value !== `${CSS_CLASS_NAME_DIRECTION}${direction}`;
      });
    });

    // add new direction
    if (piece.direction) {
      elClassList.push(`cm-piece-direction--${piece.direction}`);
    }

    return elClassList.join(" ");
  } else {
    // new piece
    return `cm-piece cm-piece--${
      piece.type.name
    } ${PIECE_CSS_CLASS_FOR_JS_BASE} ${
      piece.direction
        ? `cm-piece-direction cm-piece-direction--${piece.direction}`
        : ""
    } ${PIECE_CSS_CLASS_FOR_JS}${piece.playerId}`;
  }
}

export function getPieceCssClassName(pieceId) {
  return `${PIECE_CSS_CLASS_FOR_JS}${pieceId}`;
}

export function handleLoweringPiecesUi({
  aiPieces,
  boardPositionsRaised,
  humanPieces,
}) {
  boardPositionsRaised.forEach((pos) => {
    lowerBoardPositionUi(pos);
    humanPieces.forEach((humanPiece) => {
      if (humanPiece.boardPosition === pos) {
        lowerPieceUi(humanPiece.playerId);
      }
    });
    aiPieces.forEach((aiPiece) => {
      if (aiPiece.boardPosition === pos) {
        lowerPieceUi(aiPiece.playerId);
      }
    });
  });
  return []; // boardPositionsRaised becomes empty
}

export function handleRaisingPiecesUi({
  aiPieces,
  boardPositionsRaised,
  humanPieces,
}) {
  boardPositionsRaised.forEach((pos) => {
    humanPieces.forEach((humanPiece) => {
      if (humanPiece.boardPosition === pos) {
        raisePieceUi(humanPiece.playerId);
      }
    });
    aiPieces.forEach((aiPiece) => {
      if (aiPiece.boardPosition === pos) {
        raisePieceUi(aiPiece.playerId);
      }
    });
  });

  return boardPositionsRaised;
}

export function initActionsUi(actions, boardSquareCssClassName) {
  actions.forEach((action) => {
    addActionToUi(action, boardSquareCssClassName);
  });
}

export function initAiPiecesUi(
  aiPieces,
  boardSizeX,
  boardSizeY,
  boardCssClassName
) {
  if (aiPieces && boardSizeX && boardSizeY) {
    aiPieces.forEach((aiPiece) => {
      addPieceToBoardUi(aiPiece, boardSizeX, boardSizeY, boardCssClassName);
    });
  }
}

export function initBoardPositionsMissingUi(
  boardPositionsMissing,
  boardSquareCssClassName
) {
  if (boardPositionsMissing) {
    boardPositionsMissing.forEach((boardPosition) => {
      setBoardPositionMissingUi(boardPosition, boardSquareCssClassName);
    });
  }
}

export function initBoardPositionsRaisedUi(
  boardPositionsRaised,
  boardSquareCssClassName
) {
  if (boardPositionsRaised) {
    boardPositionsRaised.forEach((boardPosition) => {
      raiseBoardPositionUi(boardPosition, boardSquareCssClassName);
    });
  }
}

export function initHumanPiecesUi(
  humanPieces,
  boardSizeX,
  boardSizeY,
  boardCssClassName
) {
  humanPieces.forEach((human) => {
    addPieceToBoardUi(human, boardSizeX, boardSizeY, boardCssClassName);
    updatePlayerLivesUi(human.lives);
  });
}

export function lowerBoardPositionUi(boardPosition) {
  document
    .querySelector(`.${BOARD_SQUARE_CSS_CLASS_NAME}${boardPosition}`)
    .classList.remove("is-raised");
}

export function lowerPieceUi(playerId) {
  document
    .querySelector(`.${PIECE_CSS_CLASS_FOR_JS}${playerId}`)
    .classList.remove("is-raised");
}

export function movePiece(
  pieceDOMElement,
  squareToMoveTo,
  boardSizeX,
  boardSizeY
) {
  const numberOfSquaresWide = boardSizeX;
  const numberOfSquaresHigh = boardSizeY;
  let colNumber = squareToMoveTo % numberOfSquaresWide;

  if (colNumber === 0) {
    colNumber = numberOfSquaresWide - 1;
  } else {
    colNumber = colNumber - 1;
  }

  const rowNumber = Math.ceil(squareToMoveTo / numberOfSquaresHigh) - 1;

  document.querySelector(`.${pieceDOMElement}`).style.top = `calc(${
    rowNumber * 100
  }% / ${numberOfSquaresHigh})`;
  document.querySelector(`.${pieceDOMElement}`).style.left = `calc(${
    colNumber * 100
  }% / ${numberOfSquaresWide})`;
}

export function playHitAnimationUi(boardPosition) {
  const element = document.querySelector(
    `.${BOARD_SQUARE_CSS_CLASS_NAME}${boardPosition}`
  );
  element.classList.add("is-hit-animation");
  setTimeout(function () {
    element.classList.remove("is-hit-animation");
  }, 2000);
}

export function raiseBoardPositionUi(
  boardPosition,
  boardSquareCssClassName = BOARD_SQUARE_CSS_CLASS_NAME
) {
  document
    .querySelector(`.${boardSquareCssClassName}${boardPosition}`)
    .classList.add("is-raised");
}

export function raisePieceUi(playerId) {
  document
    .querySelector(`.${PIECE_CSS_CLASS_FOR_JS}${playerId}`)
    .classList.add("is-raised");
}

function removeActionsFromBoard() {
  document
    .querySelectorAll(`.${CSS_CLASS_NAME_FOR_ACTION}`)
    .forEach((el) =>
      el.classList.remove("cm-action", "js-action", `cm-action--${ACTION_EXIT}`)
    );
}

export function removePieceFromBoardUi(pieceId) {
  document.querySelector(`.${PIECE_CSS_CLASS_FOR_JS}${pieceId}`).remove();
}

function removePiecesFromBoard() {
  document.querySelectorAll(`.${PIECE_CSS_CLASS_FOR_JS_BASE}`).forEach((el) => {
    el.remove();
  });
}

export function setBoardPositionMissingUi(
  boardPosition,
  boardSquareCssClassName = BOARD_SQUARE_CSS_CLASS_NAME
) {
  document
    .querySelector(`.${boardSquareCssClassName}${boardPosition}`)
    .classList.add("is-missing");
}

export function setTargetBoardPositionsUi(boardPositions) {
  boardPositions.forEach((position) => {
    document
      .querySelector(`.${BOARD_SQUARE_CSS_CLASS_NAME}${position}`)
      .classList.add("is-target");
  });
}

export function setPieceDidFallUi(piece) {
  const el = document.querySelector(
    `.${PIECE_CSS_CLASS_FOR_JS}${piece.playerId}`
  );

  if (el) {
    el.classList.add("did-fall");
  }
}

export function showMessageUi(message) {
  document.querySelector(`.${CSS_CLASS_NAME_MESSAGE}`).innerHTML = message;
}

export function updatePieceDirectionUi(piece) {
  const cssClassName = generatePieceClassName(piece);

  document.querySelector(
    `.${PIECE_CSS_CLASS_FOR_JS}${piece.playerId}`
  ).className = cssClassName;
}

export function updatePlayerLivesUi(numberOfLives) {
  const playerLivesDomContainer = document.querySelector(".js-lives-counter");
  if (playerLivesDomContainer) {
    playerLivesDomContainer.innerHTML = numberOfLives;
  }
}

export function togglePieceWeaponUi({ piece, weaponType }) {
  const pieceDom = document.querySelector(
    `.${PIECE_CSS_CLASS_FOR_JS}${piece.playerId}`
  );
  if (pieceDom) {
    pieceDom.classList.toggle(`has-${weaponType}`);
  }
}
