import {
  KEYCODE_UP,
  KEYCODE_DOWN,
  KEYCODE_LEFT,
  KEYCODE_RIGHT,
  KEYCODE_UP_LEFT,
  KEYCODE_UP_RIGHT,
  KEYCODE_DOWN_RIGHT,
  KEYCODE_DOWN_LEFT,
  ALL_DIRECTIONS,
} from "./keyboard";
import { togglePieceWeaponUi } from "./ui";
import { capitalize } from "./utils";

class Piece {
  getRandomDirection() {
    return this.directions[Math.floor(Math.random() * this.directions.length)];
  }
}

export class Queen extends Piece {
  constructor() {
    super();
    this.directions = ALL_DIRECTIONS;
    this.name = "queen";
  }
}
export class King extends Piece {
  constructor() {
    super();
    this.directions = ALL_DIRECTIONS;
    this.name = "king";
  }
}
export class Rook extends Piece {
  constructor() {
    super();
    this.directions = [KEYCODE_UP, KEYCODE_DOWN, KEYCODE_LEFT, KEYCODE_RIGHT];
    this.name = "rook";
  }
}
export class Bishop extends Piece {
  constructor() {
    super();
    this.directions = [
      KEYCODE_UP_LEFT,
      KEYCODE_UP_RIGHT,
      KEYCODE_DOWN_RIGHT,
      KEYCODE_DOWN_LEFT,
    ];
    this.name = "bishop";
  }
}

export function getOppositeDirection(direction) {
  switch (direction) {
    case KEYCODE_UP:
      return KEYCODE_DOWN;
    case KEYCODE_DOWN:
      return KEYCODE_UP;
    case KEYCODE_LEFT:
      return KEYCODE_RIGHT;
    case KEYCODE_RIGHT:
      return KEYCODE_LEFT;
    case KEYCODE_UP_LEFT:
      return KEYCODE_DOWN_RIGHT;
    case KEYCODE_UP_RIGHT:
      return KEYCODE_DOWN_LEFT;
    case KEYCODE_DOWN_LEFT:
      return KEYCODE_UP_RIGHT;
    case KEYCODE_DOWN_RIGHT:
      return KEYCODE_UP_LEFT;
  }

  return direction;
}

export function togglePieceWeapon({ piece, hasWeapon, weaponType }) {
  togglePieceWeaponUi({
    piece,
    weaponType,
  });
  const weaponTypeCapitalized = capitalize(weaponType);
  piece[`has${weaponTypeCapitalized}`] = hasWeapon;

  return piece;
}
