export const ACTION_EXIT = "exit";
export const ACTION_EXIT_CLOSED = "exit--closed";
export const ACTION_KEY = "key";
export const ACTION_LEVER_OFF = "lever-off";
export const ACTION_LEVER_ON = "lever-on";
export const ACTION_STAIRS = "stairs";
export const ACTION_SWORD = "sword";
export const ACTION_SHIELD = "shield";
export const ACTIONS_ALLOWED_FOR_AI = [ACTION_LEVER_OFF, ACTION_LEVER_ON];
import {
  MESSAGE_KEY_PICK_UP,
  MESSAGE_DOOR_LOCKED,
  MESSAGE_SHIELD_PICK_UP,
  MESSAGE_SWORD_PICK_UP,
} from "./dictionary";

import { addActionToUi, clearActionUi, showMessageUi } from "./ui";

export function handleActionKey({ action, indexOfAction, levelActions }) {
  // avoid mutation so create new array
  const newLevelActions = JSON.parse(JSON.stringify(levelActions));
  // remove the key action
  clearActionUi(ACTION_KEY, action.boardPosition);
  showMessageUi(MESSAGE_KEY_PICK_UP);
  newLevelActions.splice(indexOfAction, 1);

  // remove closed exit and replace with open exit
  const actionExitClosedIndex = newLevelActions.findIndex(
    (action) => action.type === ACTION_EXIT_CLOSED
  );
  if (actionExitClosedIndex > -1) {
    newLevelActions[actionExitClosedIndex].type = ACTION_EXIT;
    clearActionUi(
      ACTION_EXIT_CLOSED,
      newLevelActions[actionExitClosedIndex].boardPosition
    );
    const actionExitOpenIndex = actionExitClosedIndex;
    addActionToUi(newLevelActions[actionExitOpenIndex]);
  }

  return newLevelActions;
}

export function handleExitClosed() {
  showMessageUi(MESSAGE_DOOR_LOCKED);
}

export function handleActionLeverOff({ indexOfLeverOff, levelActions }) {
  // avoid mutation so create new array
  const newLevelActions = JSON.parse(JSON.stringify(levelActions));
  showMessageUi("The lever was pulled on");
  newLevelActions[indexOfLeverOff].type = ACTION_LEVER_ON;
  clearActionUi(
    ACTION_LEVER_OFF,
    newLevelActions[indexOfLeverOff].boardPosition
  );
  const indexOfLeverOn = indexOfLeverOff;
  addActionToUi(newLevelActions[indexOfLeverOn]);

  return newLevelActions;
}

