export const KEYCODE_UP = "ArrowUp";
export const KEYCODE_DOWN = "ArrowDown";
export const KEYCODE_LEFT = "ArrowLeft";
export const KEYCODE_RIGHT = "ArrowRight";
export const KEYCODE_UP_LEFT = "KeyQ";
export const KEYCODE_UP_RIGHT = "KeyP";
export const KEYCODE_DOWN_LEFT = "KeyZ";
export const KEYCODE_DOWN_RIGHT = "KeyM";
export const KEYCODE_RESTART = "KeyR";
const KEYCODE_ENTER = "Enter";
export const ALL_DIRECTIONS = [
  KEYCODE_UP,
  KEYCODE_DOWN,
  KEYCODE_LEFT,
  KEYCODE_RIGHT,
  KEYCODE_UP_LEFT,
  KEYCODE_UP_RIGHT,
  KEYCODE_DOWN_LEFT,
  KEYCODE_DOWN_RIGHT,
];

export function initUserControls(
  handleUserInputMove,
  showNextMessage,
  restartLevel
) {
  window.addEventListener("keyup", (e) => {
    if (
      ![
        KEYCODE_UP,
        KEYCODE_DOWN,
        KEYCODE_LEFT,
        KEYCODE_RIGHT,
        KEYCODE_UP_LEFT,
        KEYCODE_UP_RIGHT,
        KEYCODE_DOWN_LEFT,
        KEYCODE_DOWN_RIGHT,
        KEYCODE_ENTER,
        KEYCODE_RESTART,
      ].includes(e.code)
    ) {
      return;
    }

    switch (e.code) {
      case KEYCODE_ENTER:
        showNextMessage();
        break;
      case KEYCODE_RESTART:
        restartLevel();
        break;
      default:
        handleUserInputMove(e.code);
        break;
    }
  });
}
