import {
  ACTIONS_ALLOWED_FOR_AI,
  ACTION_EXIT,
  ACTION_EXIT_CLOSED,
  ACTION_KEY,
  ACTION_LEVER_OFF,
  ACTION_LEVER_ON,
  ACTION_SWORD,
  ACTION_SHIELD,
  handleActionKey,
  handleExitClosed,
  handleActionLeverOff,
  handleActionLeverOn,
  handleActionSword,
  handleActionShield,
} from "./actions";
import {
  getNextBoardPosition,
  getBoardPositionsOccupied,
  getBoardPositionsUnavailable,
  getBoardPositionsAPieceCantBeShovedOnto,
  getAllBoardCheckPositions,
  handleShovePiece,
} from "./board";
import {
  MESSAGE_HUMAN_CHECK,
  MESSAGE_HUMAN_TAKEN,
  MESSAGE_KILLED_AI_SWORD,
  MESSAGE_PIECE_FELL,
} from "./dictionary";
import { initUserControls } from "./keyboard";
import { clearLevel, loadLevel, showLevelMessage } from "./level";
import { getAiWithDirectionsForNextMove, moveAi } from "./ai";
import {
  PIECE_MOVE_ANIMATION_TIME_MS,
  createBoardUi,
  clearTargetBoardPositionsUi,
  handleLoweringPiecesUi,
  handleRaisingPiecesUi,
  initActionsUi,
  initAiPiecesUi,
  initBoardPositionsMissingUi,
  initBoardPositionsRaisedUi,
  initHumanPiecesUi,
  movePiece,
  playHitAnimationUi,
  removePieceFromBoardUi,
  setPieceDidFallUi,
  showMessageUi,
} from "./ui";
import { togglePieceWeapon } from "./piece";

const gameState = {
  levelState: {},
  nextLevelNumber: 0,
  showTargetLines: true,
  init: function () {
    initUserControls(
      this.handleUserInputMove.bind(this),
      this.showNextMessage.bind(this),
      this.restartLevel.bind(this)
    );
    this.initLevel();
  },
  initLevel: function () {
    clearLevel();
    this.levelState.levelOver = false;
    this.levelState = loadLevel(this.nextLevelNumber);
    console.log(this.levelState);
    this.nextLevelNumber++;
    this.initLevelMessages(this.levelState.levelMessages);
    this.showNextMessage();
    createBoardUi({
      boardSizeX: this.levelState.boardSize.boardSizeX,
      boardSizeY: this.levelState.boardSize.boardSizeY,
    });
    initHumanPiecesUi(
      this.levelState.humanPieces,
      this.levelState.boardSize.boardSizeX,
      this.levelState.boardSize.boardSizeY
    );
    initAiPiecesUi(
      this.levelState.aiPieces,
      this.levelState.boardSize.boardSizeX,
      this.levelState.boardSize.boardSizeY
    );
    initActionsUi(this.levelState.actions);
    initBoardPositionsRaisedUi(this.levelState.boardPositionsRaised);
    initBoardPositionsMissingUi(this.levelState.boardPositionsMissing);
    this.levelState.currentBoardPositionsRaised = this.levelState.boardPositionsRaised;
    this.endMove();
  },
  beforeMove: function () {
    this.isMoving = true;
    if (this.showTargetLines) {
      clearTargetBoardPositionsUi();
    }
  },
  checkActionsInterceptHuman: function () {
    const indexOfLevelStateAction = this.levelState.actions.findIndex(
      (action) => {
        return (
          action.boardPosition === this.levelState.humanPieces[0].boardPosition
        );
      }
    );
    if (indexOfLevelStateAction > -1) {
      this.doBoardAction(indexOfLevelStateAction);
    }
  },
  checkActionsInterceptAi: function () {
    if (this.levelState.aiPieces && this.levelState.aiPieces.length) {
      this.levelState.aiPieces.forEach((aiPiece) => {
        const indexOfLevelStateAction = this.levelState.actions.findIndex(
          (action) => {
            return action.boardPosition === aiPiece.boardPosition;
          }
        );
        if (indexOfLevelStateAction > -1) {
          if (
            ACTIONS_ALLOWED_FOR_AI.includes(
              this.levelState.actions[indexOfLevelStateAction].type
            )
          ) {
            this.doBoardAction(indexOfLevelStateAction);
          }
        }
      });
    }
  },
  checkHumanInCheck: function () {
    if (this.levelState.aiPieces && this.levelState.aiPieces.length) {
      const allCheckBoardPositions = getAllBoardCheckPositions({
        aiPieces: this.levelState.aiPieces,
        boardPositionsOccupied: this.levelState.boardPositionsOccupied,
        boardSizeX: this.levelState.boardSize.boardSizeX,
        boardSizeY: this.levelState.boardSize.boardSizeY,
        currentBoardPositionsRaised: this.levelState
          .currentBoardPositionsRaised,
        showTargetLines: this.showTargetLines,
      });

      if (allCheckBoardPositions && allCheckBoardPositions.length) {
        this.checkTargetLinesIntercept(allCheckBoardPositions);
      }
    }
  },
  checkHumanInterceptsAi: function () {
    if (this.levelState.aiPieces && this.levelState.aiPieces.length) {
      this.levelState.aiPieces = this.levelState.aiPieces.filter((ai) => {
        const humanThatWasInterceptedIndex = this.levelState.humanPieces.findIndex(
          (human) => {
            return human.boardPosition === ai.boardPosition;
          }
        );
        let humanThatWasIntercepted = this.levelState.humanPieces[
          humanThatWasInterceptedIndex
        ];
        if (humanThatWasInterceptedIndex === -1) {
          return true;
        } else if (humanThatWasIntercepted.hasSword) {
          removePieceFromBoardUi(ai.playerId);
          showMessageUi(MESSAGE_KILLED_AI_SWORD);
          humanThatWasIntercepted = togglePieceWeapon({
            piece: humanThatWasIntercepted,
            hasWeapon: false,
            weaponType: ACTION_SWORD,
          });
          return false;
        } else if (humanThatWasIntercepted.hasShield) {
          // side effect - ai boardPosition updated
          const unavailablePositions = getBoardPositionsAPieceCantBeShovedOnto({
            aiPieces: this.levelState.aiPieces,
            humanPieces: this.levelState.humanPieces,
            boardPositionsRaised: this.levelState.currentBoardPositionsRaised,
          });
          ai = handleShovePiece({
            piece: ai,
            directionOfShove: humanThatWasIntercepted.lastUserInput,
            boardPositionsUnavailable: unavailablePositions,
            boardSizeX: this.levelState.boardSize.boardSizeX,
            boardSizeY: this.levelState.boardSize.boardSizeY,
          });
          humanThatWasIntercepted = togglePieceWeapon({
            piece: humanThatWasIntercepted,
            hasWeapon: false,
            weaponType: ACTION_SHIELD,
          });
          return !!ai;
        } else {
          removePieceFromBoardUi(humanThatWasIntercepted.playerId);
          showMessageUi(MESSAGE_HUMAN_TAKEN);
          this.levelState.levelOver = true;
          return true;
        }
      });
    }
  },
  checkMissingBoardPositionForHuman: function () {
    if (
      this.levelState.boardPositionsMissing &&
      this.levelState.boardPositionsMissing.length &&
      this.levelState.boardPositionsMissing.includes(
        this.levelState.humanPieces[0].boardPosition
      )
    ) {
      setPieceDidFallUi(this.levelState.humanPieces[0]);
      this.levelState.levelOver = true;
      showMessageUi(MESSAGE_PIECE_FELL);
    }
  },
  checkMissingBoardPositionsForAi: function () {
    if (
      this.levelState.boardPositionsMissing &&
      this.levelState.boardPositionsMissing.length &&
      this.levelState.aiPieces &&
      this.levelState.aiPieces.length
    ) {
      this.levelState.aiPieces = this.levelState.aiPieces.filter((ai) => {
        if (this.levelState.boardPositionsMissing.includes(ai.boardPosition)) {
          setPieceDidFallUi(ai);
          return false;
        } else {
          return true;
        }
      });
    }
  },
  checkTargetLinesIntercept: function (allCheckBoardPositions) {
    if (
      allCheckBoardPositions.includes(
        this.levelState.humanPieces[0].boardPosition
      )
    ) {
      playHitAnimationUi(this.levelState.humanPieces[0].boardPosition);
      this.levelState.levelOver = true;
      showMessageUi(MESSAGE_HUMAN_CHECK);
    }
  },
  doBoardAction: function (indexOfLevelStateAction) {
    const action = this.levelState.actions[indexOfLevelStateAction];
    switch (action.type) {
      case ACTION_EXIT:
        setTimeout(() => {
          this.initLevel(this.nextLevelNumber);
        }, PIECE_MOVE_ANIMATION_TIME_MS);
        break;
      case ACTION_EXIT_CLOSED:
        handleExitClosed();
        break;
      case ACTION_KEY:
        this.levelState.actions = handleActionKey({
          action,
          indexOfAction: indexOfLevelStateAction,
          levelActions: this.levelState.actions,
        });
        break;
      case ACTION_LEVER_OFF:
        this.levelState.actions = handleActionLeverOff({
          indexOfLeverOff: indexOfLevelStateAction,
          levelActions: this.levelState.actions,
        });
        initBoardPositionsRaisedUi(this.levelState.boardPositionsRaised);
        this.levelState.currentBoardPositionsRaised = handleRaisingPiecesUi({
          aiPieces: this.levelState.aiPieces,
          boardPositionsRaised: this.levelState.boardPositionsRaised,
          humanPieces: this.levelState.humanPieces,
        });
        break;
      case ACTION_LEVER_ON:
        this.levelState.actions = handleActionLeverOn({
          indexOfLeverOn: indexOfLevelStateAction,
          levelActions: this.levelState.actions,
        });
        this.levelState.currentBoardPositionsRaised = handleLoweringPiecesUi({
          aiPieces: this.levelState.aiPieces,
          boardPositionsRaised: this.levelState.boardPositionsRaised,
          humanPieces: this.levelState.humanPieces,
        });
        break;
      case ACTION_SWORD:
        this.levelState.actions = handleActionSword({
          action,
          indexOfAction: indexOfLevelStateAction,
          levelActions: this.levelState.actions,
        });
        this.levelState.humanPieces[0] = togglePieceWeapon({
          piece: this.levelState.humanPieces[0],
          hasWeapon: true,
          weaponType: ACTION_SWORD,
        });
        break;
      case ACTION_SHIELD:
        this.levelState.actions = handleActionShield({
          action,
          indexOfAction: indexOfLevelStateAction,
          levelActions: this.levelState.actions,
        });
        this.levelState.humanPieces[0] = togglePieceWeapon({
          piece: this.levelState.humanPieces[0],
          hasWeapon: true,
          weaponType: ACTION_SHIELD,
        });
        break;
    }
  },
  endMove: function () {
    this.checkActionsInterceptHuman();
    if (!this.levelState.levelOver) {
      setTimeout(() => {
        this.checkHumanInterceptsAi();
        this.checkMissingBoardPositionForHuman();
        this.checkMissingBoardPositionsForAi();
        this.updateBoardPositionsUnavailable();
        this.updateBoardPositionsOccupied();
        this.checkActionsInterceptAi();
        this.checkHumanInCheck();
        this.updateAiDirections();
        this.isMoving = false;
      }, PIECE_MOVE_ANIMATION_TIME_MS);
    }
  },
  handleMove: function (keyCode) {
    const squareToMoveTo = getNextBoardPosition({
      boardPositionsUnavailable: this.levelState.currentBoardPositionsRaised,
      boardSizeX: this.levelState.boardSize.boardSizeX,
      boardSizeY: this.levelState.boardSize.boardSizeY,
      currentBoardPosition: this.levelState.humanPieces[0].boardPosition,
      direction: keyCode,
    });
    if (squareToMoveTo === -1) {
      return;
    }

    this.levelState.humanPieces[0].boardPosition = squareToMoveTo;
    movePiece(
      "js-piece--human-one",
      squareToMoveTo,
      this.levelState.boardSize.boardSizeX,
      this.levelState.boardSize.boardSizeY
    );

    if (this.levelState.aiPieces && this.levelState.aiPieces.length) {
      this.levelState.aiPieces = moveAi({
        aiPieces: this.levelState.aiPieces,
        boardPositionsUnavailable: this.levelState.boardPositionsUnavailable,
        boardSizeX: this.levelState.boardSize.boardSizeX,
        boardSizeY: this.levelState.boardSize.boardSizeY,
      });
    }
  },
  handleUserInputMove: function (keyCode) {
    this.levelState.humanPieces[0].lastUserInput = keyCode;
    if (!this.levelState.levelOver && !this.isMoving) {
      this.beforeMove();
      this.handleMove(keyCode);
      this.endMove();
    }
  },
  initLevelMessages: function (levelMessages) {
    if (levelMessages) {
      this.levelState.levelMessages = levelMessages;
      this.levelState.messageIndex = 0;
    }
  },
  restartLevel: function () {
    this.nextLevelNumber--;
    this.initLevel();
  },
  showNextMessage: function () {
    showLevelMessage(
      this.levelState.levelMessages,
      this.levelState.messageIndex
    );
    this.levelState.messageIndex++;
  },
  updateAiDirections: function () {
    if (this.levelState.aiPieces && this.levelState.aiPieces.length) {
      this.levelState.aiPieces = getAiWithDirectionsForNextMove({
        aiPieces: this.levelState.aiPieces,
        boardPositionsRaised: this.levelState.currentBoardPositionsRaised,
        boardPositionsUnavailable: this.levelState.boardPositionsUnavailable,
        boardSizeX: this.levelState.boardSize.boardSizeX,
        boardSizeY: this.levelState.boardSize.boardSizeY,
      });
    }
  },
  updateBoardPositionsOccupied: function () {
    this.levelState.boardPositionsOccupied = getBoardPositionsOccupied({
      aiPieces: this.levelState.aiPieces,
      boardPositionsRaised: this.levelState.currentBoardPositionsRaised,
      boardPositionsMissing: this.levelState.boardPositionsMissing,
    });
  },
  updateBoardPositionsUnavailable: function () {
    this.levelState.boardPositionsUnavailable = getBoardPositionsUnavailable({
      boardPositionsRaised: this.levelState.currentBoardPositionsRaised,
      boardPositionsMissing: this.levelState.boardPositionsMissing,
    });
  },
  updatePlayerLives: function (increment) {
    this.levelState.humanPieces[0].lives =
      this.levelState.humanPieces[0].lives + increment;
  },
};

export default gameState;
