import {
  KEYCODE_UP,
  KEYCODE_DOWN,
  KEYCODE_LEFT,
  KEYCODE_RIGHT,
  KEYCODE_UP_LEFT,
  KEYCODE_UP_RIGHT,
  KEYCODE_DOWN_RIGHT,
  KEYCODE_DOWN_LEFT,
} from "./keyboard";
import {
  MESSAGE_SHOVED_AI_SHIELD,
  MESSAGE_SQUASHED_AI_SHIELD,
} from "./dictionary";

import {
  getPieceCssClassName,
  movePiece,
  removePieceFromBoardUi,
  setTargetBoardPositionsUi,
  showMessageUi,
} from "./ui";

export function canMoveLeft(currentBoardPosition, boardSizeX) {
  if (currentBoardPosition && boardSizeX) {
    return (currentBoardPosition - 1) % boardSizeX !== 0;
  } else {
    return false;
  }
}

export function canMoveRight(currentBoardPosition, boardSizeX) {
  if (currentBoardPosition && boardSizeX) {
    return (currentBoardPosition + 1) % boardSizeX !== 1;
  } else {
    return false;
  }
}

export function canMoveUp(currentBoardPosition, boardSizeY) {
  if (currentBoardPosition && boardSizeY) {
    return currentBoardPosition - boardSizeY > 0;
  } else {
    return false;
  }
}

export function canMoveDown(currentBoardPosition, boardSizeX, boardSizeY) {
  if (currentBoardPosition && boardSizeY && boardSizeX) {
    return currentBoardPosition + boardSizeY <= boardSizeX * boardSizeY;
  } else {
    return false;
  }
}

export const getNextBoardPositionWithDirection = {
  [KEYCODE_UP]: function ({ currentBoardPosition, boardSizeY }) {
    return canMoveUp(currentBoardPosition, boardSizeY)
      ? currentBoardPosition - boardSizeY
      : -1;
  },
  [KEYCODE_DOWN]: function ({ currentBoardPosition, boardSizeX, boardSizeY }) {
    return canMoveDown(currentBoardPosition, boardSizeX, boardSizeY)
      ? currentBoardPosition + boardSizeY
      : -1;
  },
  [KEYCODE_LEFT]: function ({ currentBoardPosition, boardSizeX }) {
    return !canMoveLeft(currentBoardPosition, boardSizeX)
      ? -1
      : currentBoardPosition - 1;
  },
  [KEYCODE_RIGHT]: function ({ currentBoardPosition, boardSizeX }) {
    return !canMoveRight(currentBoardPosition, boardSizeX)
      ? -1
      : currentBoardPosition + 1;
  },
  [KEYCODE_UP_LEFT]: function ({
    currentBoardPosition,
    boardSizeX,
    boardSizeY,
  }) {
    return canMoveLeft(currentBoardPosition, boardSizeX) &&
      canMoveUp(currentBoardPosition, boardSizeY)
      ? currentBoardPosition - 1 - boardSizeX
      : -1;
  },
  [KEYCODE_DOWN_LEFT]: function ({
    currentBoardPosition,
    boardSizeX,
    boardSizeY,
  }) {
    return canMoveLeft(currentBoardPosition, boardSizeX) &&
      canMoveDown(currentBoardPosition, boardSizeX, boardSizeY)
      ? currentBoardPosition - 1 + boardSizeX
      : -1;
  },
  [KEYCODE_UP_RIGHT]: function ({
    currentBoardPosition,
    boardSizeX,
    boardSizeY,
  }) {
    return canMoveRight(currentBoardPosition, boardSizeX) &&
      canMoveUp(currentBoardPosition, boardSizeY)
      ? currentBoardPosition + 1 - boardSizeX
      : -1;
  },
  [KEYCODE_DOWN_RIGHT]: function ({
    currentBoardPosition,
    boardSizeX,
    boardSizeY,
  }) {
    return canMoveRight(currentBoardPosition, boardSizeX) &&
      canMoveDown(currentBoardPosition, boardSizeX, boardSizeY)
      ? currentBoardPosition + 1 + boardSizeX
      : -1;
  },
};

export function getAllBoardCheckPositions({
  aiPieces = [],
  boardPositionsOccupied = [],
  boardSizeX,
  boardSizeY,
  currentBoardPositionsRaised = [],
  showTargetLines,
}) {
  let allTargetBoardPositions = [];
  aiPieces.forEach((piece) => {
    const isPieceRaised = getIsPieceRaised({
      piece,
      boardPositionsRaised: currentBoardPositionsRaised,
    });

    if (isPieceRaised) {
      return; // a raised piece has no check targets
    } else {
      const pieceTargetBoardPositions = getTargetLines({
        piece,
        boardPositionsOccupied,
        boardSizeX: boardSizeX,
        boardSizeY: boardSizeY,
      });
      if (showTargetLines) {
        setTargetBoardPositionsUi(pieceTargetBoardPositions);
      }
      allTargetBoardPositions = allTargetBoardPositions.concat(
        pieceTargetBoardPositions
      );
    }
  });

  return allTargetBoardPositions;
}

// returns board position to move to or -1 if invalud
export function getNextBoardPosition({
  boardPositionsUnavailable = [],
  boardSizeX,
  boardSizeY,
  currentBoardPosition,
  direction,
}) {
  const moveToSquare = getNextBoardPositionWithDirection[direction]({
    currentBoardPosition,
    boardSizeX,
    boardSizeY,
  });

  return isBoardPositionUnavailable(boardPositionsUnavailable, moveToSquare)
    ? -1
    : moveToSquare;
}

export function getBoardPositionsUnavailable({
  boardPositionsRaised = [],
  boardPositionsMissing = [],
}) {
  // merge and remove duplicates
  return [...new Set([...boardPositionsRaised, ...boardPositionsMissing])];
}

export function getBoardPositionsAPieceCantBeShovedOnto({
  aiPieces = [],
  humanPieces = [],
  boardPositionsRaised = [],
}) {
  const positionsOccupiedAi = aiPieces.map((piece) => piece.boardPosition);
  const positionsOccupiedHumans = humanPieces.map(
    (piece) => piece.boardPosition
  );
  // merge and remove duplicates
  return [
    ...new Set([
      ...boardPositionsRaised,
      ...positionsOccupiedAi,
      ...positionsOccupiedHumans,
    ]),
  ];
}

export function getBoardPositionsOccupied({
  aiPieces = [],
  boardPositionsRaised = [],
  boardPositionsMissing = [],
}) {
  const positionsOccupiedAi = aiPieces.map((piece) => piece.boardPosition);
  // merge and remove duplicates
  return [
    ...new Set([
      ...positionsOccupiedAi,
      ...boardPositionsRaised,
      ...boardPositionsMissing,
    ]),
  ];
}

export function getIsPieceRaised({ piece, boardPositionsRaised }) {
  return (
    boardPositionsRaised && boardPositionsRaised.includes(piece.boardPosition)
  );
}

export function getRandomBoardPosition({ boardSizeX, boardSizeY }) {
  return Math.ceil(Math.random() * boardSizeX * boardSizeY);
}

// TODO improve return so there's no mutation of pieceTargetBoardPositions
export function getTargetLines({
  piece,
  boardPositionsOccupied = [],
  boardSizeX,
  boardSizeY,
}) {
  let pieceTargetBoardPositions = [];

  piece.type.directions.forEach((direction) => {
    const directionTargets = getTargetsWithDirection({
      boardPositionsOccupied,
      boardSizeX,
      boardSizeY,
      direction,
      piece,
    });
    pieceTargetBoardPositions = pieceTargetBoardPositions.concat(
      directionTargets
    );
  });

  return pieceTargetBoardPositions;
}

function getTargetsWithDirection({
  boardPositionsOccupied,
  boardSizeX,
  boardSizeY,
  direction,
  piece,
}) {
  let targets = [];
  let currentBoardPosition = piece.boardPosition;
  let loopCounter = 0; // so we don't get a recursive err if moveToSquare isn't working

  function getNextTarget() {
    const nextTargetSquare = getNextBoardPosition({
      boardPositionsUnavailable: boardPositionsOccupied,
      boardSizeX,
      boardSizeY,
      currentBoardPosition,
      direction,
    });
    if (nextTargetSquare !== -1 && loopCounter < 50) {
      targets.push(nextTargetSquare);
      currentBoardPosition = nextTargetSquare;
      if (!boardPositionsOccupied.includes(nextTargetSquare)) {
        loopCounter++;
        getNextTarget();
      }
    }
  }

  getNextTarget();

  return targets;
}

export function handleShovePiece({
  piece,
  directionOfShove,
  boardPositionsUnavailable,
  boardSizeX,
  boardSizeY,
}) {
  const positionToShoveTo = getNextBoardPosition({
    boardPositionsUnavailable,
    boardSizeX,
    boardSizeY,
    currentBoardPosition: piece.boardPosition,
    direction: directionOfShove,
  });
  if (positionToShoveTo === -1) {
    removePieceFromBoardUi(piece.playerId);
    showMessageUi(MESSAGE_SQUASHED_AI_SHIELD);
    return null;
  } else {
    const pieceDomClassName = getPieceCssClassName(piece.playerId);
    piece.boardPosition = positionToShoveTo;
    showMessageUi(MESSAGE_SHOVED_AI_SHIELD);
    movePiece(pieceDomClassName, positionToShoveTo, boardSizeX, boardSizeY);
    return piece;
  }
}

function isBoardPositionUnavailable(boardPositionsUnavailable, boardPosition) {
  return (
    boardPositionsUnavailable &&
    boardPositionsUnavailable.length &&
    boardPositionsUnavailable.includes(boardPosition)
  );
}
